/* for the post page only */
.posts {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    gap: 16px;
    padding: 8px 5%;
}

.posts-sub {
    background-color: #e6f2e8;
    padding: 2px 2px 8px 2px;
    text-align: center;
}

.posts-sub span {
    margin: 0 4px 12px 4px;
}

.posts-img {
    width: 100%;
    height: 280px;
    object-fit: cover;
}

@media screen and (max-width : 700px) {
    .posts-img {
        height: 400px;
    }
}

