/* for the profile page */
.profile {
    width: 100%;
    height: 89vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile::before {
    content: '';
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('./img/JMDM5fS.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
    opacity: 0.5;
}

.up {
    width: 25%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
    border-right: 1px solid black;
}

.profile-name {
    letter-spacing: 2px;
    word-spacing: 3px;
}

.up img {
    width: 100px;
}

.down {
    width: 75%;
    height: 89vh;
    overflow: scroll;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.down-sub-1 {
    height: 10%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: #96f9a9; */
}

.down-sub {
    width: 100%;
    height: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    gap: 16px;
    padding: 16px 24px;
}

.profile-img {
    width: 100%;
    object-fit: cover;
    height: 230px;
}

@media screen and (max-width : 700px) {
    .profile {
        flex-direction: column;
    }

    .up {
        width: 100%;
        gap: 1px;
        border: 0;
        height: 50%;
    }

    .down {
        width: 100%;
    }

    .profile-img {
        height: 300px;
    }
}

/* for the Single Image */
.model {
    width: 100%;
    height: 89vh;
    display: flex;
    flex-direction: column;
    padding: 12px 15%;
}

.model-up {
    height: 90%;
    padding: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.model-up img {
    object-fit: contain;
    height: 100%;
}

.model-down {
    height: 10%;
    display: flex;
    justify-content: flex-end;
    padding: 4px;
}

.model-down button {
    padding: 8px 16px;
    padding: 10px 24px;
    cursor: pointer;
    border: 0;
    background-color: #96f9a9;
    border-radius: 18px;
}

@media screen and (max-width : 700px) {
    .model {
        padding: 8px 5%;
        gap: 4px;
    }

    .model-up {
        height: 80%;
    }

    .model-down button {
        width: 100%;
    }
}